import { Component, EventEmitter, Input, OnDestroy, Output } from '@angular/core';
import { PropertyWorthState } from '@core/data-access/states/property-worth.state';
import { SnackService } from '@core/services/snack.service';
import { PropertyWorthFacade } from '@feature/property-worth/property-worth.facade';
import { ConfirmationModalComponent } from './confirmation-modal/confirmation-modal.component';
import { MatDialog } from '@angular/material/dialog';
import { ButtonType } from '@shared/components/button/button.component';
import * as marked from 'marked';
import { CommercialLeadAIResponseComponent } from '@shared/components/commercial-lead-ai-response/commercial-lead-ai-response.component';
import { ButtonModule } from '@shared/components/button/button.module';
import { SkeletonLoaderComponent } from './skeleton-loader/skeleton-loader.component';
import { NgIf } from '@angular/common';
@Component({
    selector: 'findqo-valuation-calculated',
    imports: [CommercialLeadAIResponseComponent, ButtonModule, SkeletonLoaderComponent, NgIf],
    templateUrl: './valuation-calculated.component.html',
    styleUrl: './valuation-calculated.component.scss'
})
export class ValuationCalculatedComponent {
	constructor(
		private propertyWorth: PropertyWorthFacade,
		private propertyWorthState: PropertyWorthState,
		private snack: SnackService,
		private dialog: MatDialog
	) {}

	isLoading = true;
	inPersonValuationRequested = false;
	aiContent;
	payload;
	isError = false;
	valuationId: string;
	aiResponse: any = null;
	summary: any = null;

	readonly buttonType = ButtonType;

	@Output() prevTab = new EventEmitter();

	@Input() set formData(formData) {
		if (formData) {
			this.aiContent = null;
			this.isLoading = true;
			this.inPersonValuationRequested = false;
			this.isError = false;
			this.payload = formData;
			this.postPropertyWorth();
		} else {
			this.prevTab.emit(0);
		}
	}

	postPropertyWorth() {
		const API_PAYLOAD = this.removeEmptyKeys(this.payload);
		this.isLoading = true;

		this.propertyWorthState.setProcessing(true);
		this.propertyWorth.postCommercialEstimation(API_PAYLOAD).subscribe(
			(res) => {
				this.isLoading = false;
				this.valuationId = res?.['data']?.id;

				try {
					this.aiResponse = JSON.parse(res?.['data']?.ai_response);
					this.summary = this.aiResponse?.summary || null;
				} catch (e) {
					this.isError = true;
					this.aiResponse = null;
				}

				this.propertyWorthState.setProcessing(false);

				if (this.payload?.isPersonValuation === 1) {
					this.dialog.open(ConfirmationModalComponent, {
						disableClose: false,
						maxWidth: '375px',
						width: '100%',
						height: 'auto',
						maxHeight: '100%',
					});
				}
			},
			(error) => {
				if (error?.error?.error?.id === 'invalidEmail') {
					this.prevTab.emit(1);
					this.snack.open({
						data: { message: 'Invalid Email, please try again', type: 'error' },
						panelClass: ['snackbar-error'],
					});
				}

				this.propertyWorthState.setProcessing(false);
				this.isError = true;
				this.isLoading = false;
			}
		);
	}

	removeEmptyKeys(obj) {
		const RESULT = {};

		for (const key in obj) {
			if (Object.prototype.hasOwnProperty.call(obj, key)) {
				if (obj[key] !== '' && obj[key] !== null && obj[key] !== undefined) {
					RESULT[key] = obj[key];
				}
			}
		}

		return RESULT;
	}
}
