import { Component, EventEmitter, Output } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { ButtonType } from '@shared/components/button/button.component';
import { map, Observable, startWith } from 'rxjs';
import { Ericode } from '@core/models';
import { DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE, NativeDateAdapter } from '@angular/material/core'; // Import MatNativeDateAdapter
import * as counties from '../../../../shared/json/counties/counties.json';

// Custom date adapter based on the native DateAdapter
export class MyDateAdapter extends NativeDateAdapter {
	override format(date: Date, displayFormat: Object): string {
		const YEAR = date.getFullYear();

		return `${YEAR}`;
	}
}

export const MY_FORMATS = {
	parse: {
		dateInput: 'yyyy',
	},
	display: {
		dateInput: 'yyyy',
		monthYearLabel: 'yyyy',
		dateA11yLabel: 'LL',
		monthYearA11yLabel: 'yyyy',
	},
};

@Component({
    selector: 'findqo-commercial-property-information',
    templateUrl: './property-information.component.html',
    styleUrl: './property-information.component.scss',
    providers: [
        {
            provide: DateAdapter,
            useClass: MyDateAdapter,
            deps: [MAT_DATE_LOCALE],
        },
        {
            provide: MAT_DATE_FORMATS,
            useValue: MY_FORMATS,
        },
    ],
    standalone: false
})
export class PropertyInformationComponent {
	constructor(private fb: FormBuilder) {}

	propertyInformationForm = this.fb.group({
		propertyIntent: ['lease', [Validators.required]],
		county: ['', [Validators.required]],
		propertySize: ['', [Validators.required]],
		propertyUnit: ['people'],
		budgetPeriod: ['monthly'],
		comment: ['', [Validators.required]],
	});

	@Output() nextTab = new EventEmitter();

	maxDate = new Date();

	readonly buttonType = ButtonType;

	counties = counties?.data?.all;
	eirCodeOptions$: Observable<Ericode[]>;
	filteredCounties: Observable<any[]>;

	units = [
		{
			key: 'people',
			label: 'People office',
		},
	];

	budgetPeriods = [
		{
			key: 'monthly',
			label: 'Monthly',
		},
		{
			key: 'annually',
			label: 'Annually',
		},
	];

	filterCounties(value: string) {
		const filterValue = value.toLowerCase();
		return this.counties.filter((county) => county?.name.toLowerCase().includes(filterValue));
	}

	onNext() {
		const FORM_DATA = this.propertyInformationForm.value;
		this.nextTab.emit({
			status: this.propertyInformationForm.status,
			value: {
				...FORM_DATA,
			},
			form: 'propertyInformation',
		});
	}

	updateTransactionType(type: string) {
		this.propertyInformationForm.get('propertyIntent').setValue(type);
	}

	selectCounty(county: string) {
		this.propertyInformationForm.get('county').setValue(county);
	}

	ngOnInit() {
		this.filteredCounties = this.propertyInformationForm.controls['county'].valueChanges.pipe(
			startWith(''),
			map((value) => this.filterCounties(value || ''))
		);
	}
}
